@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.body {
    margin: 0;
    padding: 0;
  }
  .header {
    margin: 0 auto;
    padding: 0 auto;
    min-width: 1750px;
    align-items: center;
    height: 70px;
    background: #fff;
    grid-template-columns: 34% 5% 4% 3% 0% 5% 3% 5%;
    grid-template-rows: 1fr;
    display: grid;
  }
  
  .header__search {
    width: 16px;
    height: 16px;
    cursor: pointer;
  }
  
  .n__header__input {
    border: none;
    width: 300px;
    padding: 0px 11px;
    cursor: pointer;
  }
  
  .header__search__input {
    padding: 0px 11px;
    cursor: pointer;
  }
  
  .header__avatar {
    width: 38px;
    height: 38px;
    cursor: pointer;
  }
  
  .header__planet {
    width: 16px;
    height: 16px;
    opacity: 1;
    cursor: pointer;
  }
  
  .header__name {
    display: inline-grid;
    margin: 0;
    cursor: pointer;
  }
  
  @media (min-width: 768px) {
    .header {
      max-width: 720px;
    }
  }
  
